
import React, { useRef, useState } from 'react'
import { Form, Button, Card, Alert } from 'react-bootstrap'
import { Link, useNavigate } from 'react-router-dom'
import { useAuth } from '../contexts/AuthContext'

export default function ResetPassword(){
    const passwordRef = useRef()
    const passwordConfirmRef = useRef()
    const { currentUser, updatePassword } = useAuth()
    const [error, setError] = useState('')
    const [loading, setLoading] = useState(false)
    const navigate = useNavigate()

    function handleSubmit(e) {
        e.preventDefault()
        if (passwordRef.current.value !== passwordConfirmRef.current.value) {
          return setError("Passwords do not match")
        }

        const promises = []
        setLoading(true)
        setError("")

        if(passwordRef.current.value){
            promises.push(updatePassword(passwordRef.current.value))
        }

        Promise.all(promises).then(()=>{
            navigate('/')
        }).catch(()=>{
            setError("Failed to update Account")
        }).finally(()=>{
            setLoading(false)
        })
      }

    return(
        <>
            <Card>
                <Card.Body>
                    <h2 className='text-center mb-4'>Change Password</h2>
                    {error && <Alert variant="danger">{error}</Alert>}
                    <Form onSubmit={handleSubmit}>
                        <Form.Group id="email">
                            <strong>Email:</strong><br/>
                            {currentUser.email}
                        </Form.Group>
                        <Form.Group id="password" className='mt-3 mb-1'>
                            <Form.Label>Password</Form.Label>
                            <Form.Control type="password" ref={passwordRef} placeholder='Insert new password'/>
                        </Form.Group>
                        <Form.Group id="password-confirm" className='mt-2'>
                            <Form.Label>Password Confirmation</Form.Label>
                            <Form.Control type="password" ref={passwordConfirmRef} placeholder='Confirm new password'/>
                        </Form.Group>
                        <Button disabled={loading} className='w-100 mt-3' type='submit'>Update Password</Button>
                    </Form>
                </Card.Body>
            </Card>
            <div className='w-100 text-center mt-2'><Link to="/user-settings">Cancel</Link></div>
        </>
    )
}