import React, { useState } from 'react'
import { Alert, Button, Card } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import { useAuth } from '../contexts/AuthContext'
import { useNavigate } from 'react-router-dom'

export default function Dashboard() {
    const [error, setError] = useState('')
    const { currentUser, logout } = useAuth()
    const navigate = useNavigate()
   
    async function handleLogout(){
        setError('')
        try{
            await logout()
            navigate("/login");
        }catch{
            setError('Failed to Logout')
        }
    }

  return (
    <>
        <Card>
            <Card.Body>
                <h2 className='text-center mt-2'>Profile</h2>
                {error && <Alert variant="danger">{error}</Alert>}
                <strong>Email:</strong> {currentUser.email}
                <Link to="/user-settings" className='btn btn-primary w-100 mt-3'>User Settings</Link>
            </Card.Body>
        </Card>
        <div className='w-100 text-center mt-2'><Button variant='link' onClick={handleLogout}>Logout</Button></div>
    </>
  )
}
